<template>
    <div class="container">
        <div class="row">
            <div class="col-sm-6 mx-auto">
                <div class="panel panel-default">
                    <div class="panel-heading"><h3></h3></div>

                    <div class="panel-body">
                        <div v-if="info" class="info info">
                            <span class="text-danger" v-for="(value, name) in errors.info">{{ value[0] }}</span>
                            <span class="text-success" v-if="success.info">{{ success.info }}</span>
                        </div>
                        <form v-on:submit.prevent="saveForm" class="form-horizontal" ref="form" id="form_reg" method="POST" novalidate="">
                            
                            <input type="hidden" name="_token" :value="csrf">
                            <div class="form-group">

                                <div class="form-group_box">
                                    <input id="name"
                                            type="text"
                                            class="form-control"
                                            v-model="name"
                                            v-on:click="chang($event)"
                                            v-on:change="chang($event)"
                                            v-on:onfocus="chang($event)"
                                            v-on:keyup="chang($event)"
                                            data-required=" "
                                            value=""
                                            placeholder=" "
                                            autofocus>
                                    <span v-if="errors.name" class="help-block help_block_validate">
                                            <strong>{{ errors.name }}</strong>
                                        </span>
                        
                                </div>
                                
                            </div>
                            <div class="form-group">
                             <div class="form-group_box">
                                <input id="f_name"
                                        type="text"
                                        class="form-control"
                                        v-model="f_name"
                                        v-on:click="chang($event)"
                                        v-on:change="chang($event)"
                                        v-on:onfocus="chang($event)"
                                        v-on:keyup="chang($event)"
                                        data-required=" "
                                        value=""
                                        placeholder=" "
                                        autofocus>
                                <span v-if="errors.f_name" class="help-block help_block_validate">
                                        <strong>{{ errors.f_name }}</strong>
                                    </span>                    
                                </div>
                            </div>
                            <div class="form-group">

                                <div class="form-group_box">
                                    <input id="email"
                                            type="email"
                                            class="form-control"
                                            v-model="email"
                                            v-on:click="chang($event)"
                                            v-on:change="chang($event)"
                                            data-required=" E-mail"
                                            placeholder=" e-mail"
                                            required>

                                        <span v-show="errors.email" class="help-block help_block_validate">
                                            <strong>{{ errors.email }}</strong>
                                        </span>
                            
                                </div>
                            </div>
                            <div class="form-group">

                                <div class="form-group_box">
                                    <label for="">8(905)-905-00-00</label>
                                    <input id="phone"
                                            type="phone"
                                            class="form-control"
                                            v-model="phone"
                                            v-on:click="chang($event)"
                                            v-on:change="chang($event)"
                                            v-on:keyup="keyup($event)"
                                            data-required=" "
                                            placeholder=" "
                                            required>

                                        <span v-show="errors.phone" class="help-block help_block_validate">
                                            <strong>{{ errors.phone }}</strong>
                                        </span>
                            
                                </div>
                            </div>
                            <div class="form-group">

                                <div class="form-group_box">
                                    <input id="password"
                                            type="password"
                                            class="form-control"
                                            v-model="password"
                                            v-on:click="chang($event)"
                                            v-on:change="chang($event)" 
                                            data-required=" "
                                            placeholder=" "
                                            required>

                            
                                        <span v-show="errors.password" class="help-block help_block_validate">
                                            <strong>{{ errors.password }}</strong>
                                        </span>
                        
                                </div>
                            </div>

                            <div class="form-group">

                                <div class="form-group_box">
                                    <input id="password_confirmation"
                                            type="password"
                                            class="form-control"
                                            v-model="password_confirmation"
                                            v-on:click="chang($event)"
                                            v-on:change="chang($event)"
                                            data-required=" "
                                            placeholder=" ">

                                        <span v-show="errors.password_confirmation" class="help-block help_block_validate">
                                            <strong>{{ errors.password_confirmation }}</strong>
                                        </span>
                                </div>
                            </div>

                            <div class="form-group">
                                <div class="form-group_box">
                                    <button type="submit" class="btn btn-primary mx-auto">
                                        
                                    </button>
                                    <router-link class="btn btn-primary mx-auto" to="/login"></router-link>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        data() {
            return{
                name : "",
                f_name : "",
                email : "",
                phone:"",
                password : "",
                password_confirmation : "",
                errors: {
                    name:false,
                    f_name:false,
                    email: false,
                    phone:false,
                    password: false,
                    password_confirmation: false,
                    info: false
                },
                success:{info:false},
                info: false,
                csrf: document.querySelector('meta[name="csrf-token"]').getAttribute('content')
            }
        },
        methods:{
            chang:function(event) {
               // console.log(event.target);
                var $name = event.target.id;
                var val = event.target.value;
                var required = event.target.dataset.required;
              // console.log(this.$refs.form);
                if (val == "") {
                   // console.log($name);
                    this.errors[$name] = required;
                }else{
                    //console.log($name);
                    this.errors[$name] = false;
                    //this.errors[$name] = true;
                }
                if ($name == "password" && this.password != "") {
                    //console.log(this.password.length);
                    if (this.password.length < 6) {
                       this.errors[$name] = "   6 "; 
                    }
                    if (this.password_confirmation == "") {
                        this.errors.password_confirmation = " ";
                    }
                    if (this.password != this.password_confirmation) {
                        this.errors[$name] = "  "; 
                    }else{
                        this.errors[$name] = false; 
                    }
                }
                if ($name == "password_confirmation") {
                    if (this.password != this.password_confirmation) {
                        this.errors.password = "  "; 
                    }else{
                        this.errors.password = false;
                    }
                    if (this.password.length < 6) {
                       this.errors.password = "   6 "; 
                    }
                }
                let errors =false;
             //  console.log(this.errors);
            },
            keyup(event){
                //console.log(event.target.id);

                let $name = event.target.id;

                if ($name == 'phone') {

                    this.getPhoneMack(event)

                }

               // console.log(length);

            },
            getPhoneMack(event){
           // console.log(event.target.id);

            let $name = event.target.id;

             var val = event.target.value;

                val = val.replace(/[^0-9]/giu,"");

               // console.log(val);

                let length = val.length;

                if (length <11) {

                  //  console.log(val);

                  //  console.log(length);

                    this.errors[$name] = '     11 ';

                    return;
                }else{

                    this.errors[$name] = false;

                    this.phone = val;

                }

            },
            Hide(){
               this.$emit('Hide'); 
            },
            saveForm: function(event){
                // event.preventDefault(e);
                 //console.log(event);
               // console.log(this.password);

                     this.info = false;
                     this.success.info = false;
                     this.errors.info = false;

                    let errors = false;

                    if (this.name == "") {
                        this.errors.name = " ";
                        errors = true;
                    }
                    if (this.f_name == "") {
                        this.errors.f_name = " ";
                        errors = true;
                    }
                    if (this.phone == "") {
                        this.errors.phone = " ";
                        errors = true;
                    }
                    if (this.email == "") {
                        this.errors.email = " e-mail";
                        errors = true;
                    }
                    if (this.password == "") {
                        this.errors.password = " ";
                        errors = true;
                    }
                    if (this.password_confirmation == "") {
                        this.errors.password_confirmation = " ";
                        errors = true;
                    
                    }
                    if (errors) {
                         console.log(this.errors);
                        return this.errors;
                    }

                 if (this.password === this.password_confirmation)
                {
                    //console.log(this.password);
                    let url = "/api/v1/register";
                 
                    axios.post(url, {
                        name: this.name,
                        f_name: this.f_name,
                        phone: this.phone,
                        email: this.email,
                        password: this.password,
                        password_confirmation: this.password_confirmation 
                    })
                    .then(response => {
                       console.log(response.data);
                        //localStorage.setItem('user',JSON.stringify(response.data.user))
                        //localStorage.setItem('jwt',response.data.token)

                        if (response.data.result == true) {

                            this.$emit("registerIn",true);

                            this.$router.push('/email/verify/notic');

                        }

                        if (response.data.errors) {

                            console.log(response.data.errors);

                            this.errors.info = response.data.errors;
                            this.info = true;
                            
                        }else if (response.data.success) {

                            this.success.info = response.data.success;
                            this.info = true;

                            this.name = '';
                            this.email = '';
                            this.password = '';
                            this.password_confirmation = '';

                            //this.RegForm.reset();

                             event.target.reset();
                        }

                    });
                } else {
                    
                     this.errors.password = "  ";

                         console.log(this.errors);
                         return this.errors;
                }
            }
        },
        mounted() {
            console.log('Login mounted.');
            this.Hide();
        },
        created() {
            this.csrfToken = document.querySelector('meta[name="csrf-token"]').content
        }
    }
</script>
<style>
.help_block_validate {
    color: red;
}
</style>
